<template>
    <div class='ziliao'>
        <div class="title">{{$t('xiugaimima[0]')}}</div>
        <div class="hr"></div>
        <div class="item">
            <div>{{$t('xiugaimima[1]')}}：</div>
            <div>
                <el-input v-model="form.old_password" :placeholder="$t('xiugaimima[2]')"></el-input>
            </div>
            <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('xiugaimima[3]')}}：</div>
            <div>
                <el-input type="password" v-model="form.new_password" :placeholder="$t('xiugaimima[4]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div>{{$t('xiugaimima[5]')}}：</div>
            <div>
                <el-input type="password" v-model="form.confirmation_password" :placeholder="$t('xiugaimima[6]')"></el-input>
            </div>
             <div class="zhanwei"></div>
        </div>
        <div class="item">
            <div></div>
            <div>
                <div class="btn" @click="setUser()">{{$t('xiugaimima[7]')}}</div>
            </div>
             <div class="zhanwei"></div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            form:{
                old_password:'',//原密码
                new_password:'',//新密码
                confirmation_password:'',//确认密码
            }
        };
    },
    created:function(){
    },
    mounted:function(){
    },
    watch:{
    },
    methods:{
        //修改资料
        setUser(){
            var _this = this;
            if(!_this.form.old_password || !_this.form.new_password || !_this.form.confirmation_password){
                _this.$message({
                    message: _this.$t('xiugaimima[8]'),
                    type: 'warning'
                });
                return ;
            }
            _this.form.lang = _this.apis.lang
            _this.apis.post(_this.apis.update_password,_this.form).then(function(res) {
                if(res.data.code == 0){
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    }); 
                    _this.form = {
                                old_password:'',//原密码
                                new_password:'',//新密码
                                confirmation_password:'',//确认密码
                            }
                }else{
                    _this.$message({
                        message: res.data.msg,
                        type: 'info'
                    }); 
                }   
            })
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.ziliao{
    min-height: 631px;
    border: 1px solid #CACACA;
    padding: 20px 40px;
    margin-bottom: 100px;
    .hr{
        width: 98%;
        height: 1px;
        margin-left: 2%;
        margin-top: 25px;
        margin-bottom: 25px;
        background: #DEDEDE;
    }
    .title{
        color: #383838;
        font-size: 13px;
        font-weight: bold;
    }
    .item{
        display: flex;
        align-items: center;
        font-weight: bold;
        margin: 20px 0;
        &>div:nth-child(1){
            color: #383838;
            font-size: 13px;
            width: 100px;
            padding-left: 20px;
        }
        &>div:nth-child(2){
            flex: 344px;
            height: 32px;
            color: #383838;
            font-size: 13px;
        }
        .zhanwei{
            flex: 1;
        }
    }
    .btn{
        width: 57px;
        height: 26px;
        margin: 0 auto;
        margin-top: 40px;
        text-align: center;
        line-height: 26px;
        background: #FFAD01;
        font-size: 13px;
        color: #fff;
        font-weight: bold;
        cursor:pointer;
    }
}
</style>


